type Key = string | number | symbol;

export const zeroKey = (o: any) => (o === undefined ? 0 : o);

export const unique: <T>(v: T, i: number, a: T[]) => Boolean = (v, i, a) =>
  a.indexOf(v) === i;

export const numberlist = (n: number) =>
  new Array(n).fill(undefined).map((_, i) => i);

export const deepAssign: (
  obj: any,
  val: any,
  ...[key, ...rest]: (Key | ((v: any) => boolean))[]
) => any = (obj, val, key, ...rest) =>
  typeof key === "function"
    ? obj.map((v: any) =>
        key(v) ? (rest.length > 0 ? deepAssign(v, val, ...rest) : val) : v
      )
    : {
        ...obj,
        [key]: rest.length > 0 ? deepAssign(obj[key], val, ...rest) : val,
      };
