import type { Action } from "./Action";
import type { Coordinates } from "./Map";
import type { ResourceBag } from "./Resource";
import { emptyBag } from "./Resource";

export type ActionType = "swarm" | "city" | "trade" | "science";
export const ActionTypes: ActionType[] = ["swarm", "city", "trade", "science"];

type ActionState = "inactive" | "market" | "location" | "destination";

export type ActionSelection<T extends ActionType> = {
  state: ActionState;
  action?: Action<T>;
  location: Coordinates[];
  destination: Coordinates[];
  cost: { resources: ResourceBag; time: number };
};

export const noAction: ActionSelection<ActionType> = {
  state: "inactive",
  action: undefined,
  location: [],
  destination: [],
  cost: {
    resources: emptyBag,
    time: 0,
  },
};

export const nextState = (state: ActionState) =>
  ({
    inactive: "market",
    market: "location",
    location: "destination",
    destination: "inactive",
  }[state]);
