import type { Blueprint } from "./Blueprint";
import { Blueprints } from "./Blueprint";
import { NUM_PLAYERS, type Shuffle } from "./Swarms";
import { numberlist } from "./utils";

export type Market = {
  stalls: Blueprint[];
  bag: Blueprint[];
  discard: Blueprint[];
};

export const newMarket: Market = {
  stalls: [],
  bag: [...Blueprints],
  discard: [],
};

export const discardMarketToken = (market: Market) => {
  if (market.stalls.length === 0) {
    return market;
  }
  const [token, ...stalls] = market.stalls;
  return {
    ...market,
    stalls,
    discard: [...market.discard, token],
  };
};

const refillBag = (market: Market) =>
  market.bag.length === 0
    ? {
        ...market,
        bag: [...market.discard],
        discard: [],
      }
    : market;

const drawOneTokenFromBag = (currentMarket: Market, shuffle: Shuffle) => {
  const market = refillBag(currentMarket);
  if (market.bag.length === 0) {
    return market;
  }
  const [token, ...bag] = shuffle(market.bag);
  return {
    ...market,
    stalls: [...market.stalls, token],
    bag,
  };
};

export const addTokenToBag = (market: Market, token: Blueprint) => ({
  ...market,
  bag: [...market.bag, token],
});

export const refillMarketTokens: (
  market: Market,
  shuffle: Shuffle
) => Market = (market, shuffle) =>
  market.stalls.length === 0
    ? numberlist(NUM_PLAYERS + 1).reduce(
        (market) => drawOneTokenFromBag(market, shuffle),
        market
      )
    : market;
