import type { ActionType, ActionSelection } from "./ActionType";
import type { Coordinates, TerrainMap } from "./Map";
import type { TerrainType } from "./Terrain";
import { terrainFromMap } from "./Terrain";

export type Resource = "food" | "log" | "energy" | "ore" | "coin";
export const Resources: Resource[] = ["food", "log", "energy", "ore"];

const resourceFromTerrain: (type: TerrainType) => Resource = (type) => {
  switch (type) {
    case "water":
      return "food";
    case "forest":
      return "log";
    case "desert":
      return "energy";
    case "mountain":
      return "ore";
    case "city":
      return "coin";
  }
};

export const resourceFromActionType: (actionType: ActionType) => Resource = (
  actionType
) => {
  switch (actionType) {
    case "swarm":
      return "food";
    case "city":
      return "log";
    case "trade":
      return "energy";
    case "science":
      return "ore";
  }
};

export type ResourceBag = {
  [k in Resource]: number;
};

export const emptyBag: ResourceBag = Resources.reduce(
  (bag, resource) => ({ ...bag, [resource]: 0 }),
  { coin: 0 }
) as ResourceBag;

const resourcesFromTerrains = (map: TerrainMap, terrains: Coordinates[]) =>
  terrains.map((coord) => resourceFromTerrain(terrainFromMap(map, coord).type));

export const resourcesFromMap: (
  map: TerrainMap,
  selection: Coordinates[],
  bag?: ResourceBag
) => ResourceBag = (map, selection, bag = emptyBag) =>
  resourcesFromTerrains(map, selection).reduce(
    (acc, resource) => ({ ...acc, [resource]: acc[resource] + 1 }),
    bag
  );

export const addBags: (b1: ResourceBag, b2: ResourceBag) => ResourceBag = (
  b1,
  b2
) =>
  Resources.reduce(
    (acc, resource) => ({
      ...acc,
      [resource]: acc[resource] + b2[resource],
    }),
    { ...b1, coin: b1.coin + b2.coin }
  );

export const payWithCoins: (
  cost: ResourceBag,
  availableResources: ResourceBag
) => number = (cost, availableResources) =>
  Resources.reduce(
    (acc, resource) =>
      acc + Math.max(cost[resource] - availableResources[resource], 0),
    0
  );

export const actionTime = (
  actionSelection: ActionSelection<ActionType>,
  availableResources: ResourceBag
) =>
  actionSelection.cost.time +
  [...actionSelection.location, ...actionSelection.destination].length +
  Resources.reduce((acc, r) => acc + actionSelection.cost.resources[r], 0) -
  payWithCoins(actionSelection.cost.resources, availableResources);
