import { noAction, type ActionType, type ActionSelection } from "./ActionType";
import type { Blueprint } from "./Blueprint";
import { Blueprints } from "./Blueprint";
import type { TechGrid } from "./Grid";
import { playerStartingGrid } from "./Grid";
import type { Coordinates } from "./Map";
import type { Species } from "./Species";
import { SpeciesList } from "./Species";
import type { Track } from "./Track";

export type Player = {
  id: number;
  species: Species;
  blueprint: Blueprint;
  corner: Coordinates;
  timeSpent: number;
  coins: number;
  coinsSpent: number;
  swarm: Coordinates[];
  city: Coordinates[];
  actionSelection: ActionSelection<ActionType>;
  grid: TechGrid;
  travel: number;
};

export const newPlayer: (id: number, corner: Coordinates) => Player = (
  id,
  corner
) => ({
  id,
  species: SpeciesList[id],
  blueprint: Blueprints[Blueprints.length - id - 1],
  corner,
  timeSpent: 0,
  coins: 10,
  coinsSpent: 0,
  swarm: [],
  city: [],
  actionSelection: noAction,
  grid: playerStartingGrid(),
  travel: 0,
});

const remainingCoins = (player: Player) => player.coins - player.coinsSpent;

const tiebreaker = (p1: Player, p2: Player) =>
  remainingCoins(p2) > remainingCoins(p1)
    ? p2
    : remainingCoins(p2) === remainingCoins(p1) && p2.coinsSpent > p1.coinsSpent
    ? p2
    : p1;

export const winner = (players: Player[]) => players.reduce(tiebreaker);

export const nextPlayer = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) =>
  turn[
    players.reduce(
      (acc, { timeSpent }) => Math.min(timeSpent, acc),
      gameEndTriggered || players[0].timeSpent
    )
  ][0];
