import type { Blueprint } from "./Blueprint";
import type { Coordinates, TerrainMap } from "./Map";
import type { Resource } from "./Resource";

export type TerrainType = "water" | "forest" | "desert" | "mountain" | "city";
export const Terrains: TerrainType[] = [
  "water",
  "forest",
  "desert",
  "mountain",
];

export const TerrainSmoothing: TerrainType[] = [
  "water",
  "desert",
  "forest",
  "mountain",
];

export const terrainFromResource: (resource: Resource) => TerrainType = (
  resource
) => {
  switch (resource) {
    case "food":
      return "water";
    case "log":
      return "forest";
    case "energy":
      return "desert";
    case "ore":
      return "mountain";
    case "coin":
      return "city";
  }
};

type FreeTerrain = {
  state: "free";
};

type OccupiedTerrain = {
  state: "occupied";
  owner: number;
};

type BuiltTerrain = {
  state: "built";
  blueprint: Blueprint;
};

type TerrainState = FreeTerrain | OccupiedTerrain | BuiltTerrain;

export type Terrain = {
  type: TerrainType;
  coord: Coordinates;
} & TerrainState;

export const terrainFromMap: (
  map: TerrainMap,
  { row, col }: Coordinates
) => Terrain = (map, { row, col }) => map[row][col];

type BoundingBox = {
  topLeft: Coordinates;
  bottomRight: Coordinates;
};

export const getBoundingBox: (swarm: Coordinates[]) => BoundingBox = (
  swarm
) => ({
  topLeft: {
    row: Math.min(...swarm.map((o) => o.row)),
    col: Math.min(...swarm.map((o) => o.col)),
  },
  bottomRight: {
    row: Math.max(...swarm.map((o) => o.row)),
    col: Math.max(...swarm.map((o) => o.col)),
  },
});

export const terrainToIndex = (terrain: TerrainType) =>
  TerrainSmoothing.findIndex((t) => t === terrain);
