import type { Coordinates, TerrainMap } from "./Map";
import {
  containsCoords,
  getAdjacentToSelection,
  isAdjacentTo,
  isWithinMap,
  sameCoordinates,
  startCorners,
  subtractCoordinates,
} from "./Map";
import type { Player } from "./Player";
import type { Terrain } from "./Terrain";
import { getBoundingBox } from "./Terrain";

export type Blueprint = "O" | "I" | "T" | "S" | "Z" | "L" | "J";
export const Blueprints: Blueprint[] = ["O", "I", "T", "S", "L"];

const flipBlueprint: (blueprint: Blueprint) => Blueprint[] = (blueprint) =>
  ((
    {
      O: ["O"],
      I: ["I"],
      T: ["T"],
      S: ["S", "Z"],
      Z: ["Z", "S"],
      L: ["L", "J"],
      J: ["J", "L"],
    } as Record<Blueprint, Blueprint[]>
  )[blueprint]);

const BLUEPRINT_PATTERNS: { [k in Blueprint]: boolean[][][] } = {
  O: [
    [
      [true, true],
      [true, true],
    ],
  ],
  I: [[[true, true, true, true]], [[true], [true], [true], [true]]],
  T: [
    [
      [true, true, true],
      [false, true, false],
    ],
    [
      [false, true],
      [true, true],
      [false, true],
    ],
    [
      [false, true, false],
      [true, true, true],
    ],
    [
      [true, false],
      [true, true],
      [true, false],
    ],
  ],
  S: [
    [
      [true, true, false],
      [false, true, true],
    ],
    [
      [false, true],
      [true, true],
      [true, false],
    ],
  ],
  Z: [
    [
      [true, false],
      [true, true],
      [false, true],
    ],
    [
      [false, true, true],
      [true, true, false],
    ],
  ],
  L: [
    [
      [true, true, true],
      [true, false, false],
    ],
    [
      [true, true],
      [false, true],
      [false, true],
    ],
    [
      [false, false, true],
      [true, true, true],
    ],
    [
      [true, false],
      [true, false],
      [true, true],
    ],
  ],
  J: [
    [
      [true, true],
      [true, false],
      [true, false],
    ],
    [
      [true, true, true],
      [false, false, true],
    ],
    [
      [false, true],
      [false, true],
      [true, true],
    ],
    [
      [true, false, false],
      [true, true, true],
    ],
  ],
};

const getBlueprintCoordinates: (
  blueprint: Blueprint,
  coord: Coordinates,
  fr: number
) => Coordinates[] = (blueprint, coord, rotation) =>
  flipBlueprint(blueprint)
    .flatMap((bp) => BLUEPRINT_PATTERNS[bp])
    [rotation].map((row, i) =>
      row.map((col, j) =>
        col
          ? {
              row: coord.row + i,
              col: coord.col + j,
            }
          : null
      )
    )
    .flatMap((row) => row.filter((col) => col !== null) as Coordinates[]);

export const getAllBlueprintCoordinatesWithTranslation: (
  blueprint: Blueprint,
  coord: Coordinates
) => Coordinates[][] = (blueprint, coord) =>
  flipBlueprint(blueprint)
    .flatMap((bp) => BLUEPRINT_PATTERNS[bp])
    .map((rotation, irotation) =>
      rotation
        .flatMap((row, i) =>
          row.map((_col, j) => ({ row: coord.row - i, col: coord.col - j }))
        )
        .map((c) => getBlueprintCoordinates(blueprint, c, irotation))
        .filter((bp) => bp.every(isWithinMap))
    )
    .flat();

const samePattern: (
  pattern1: Coordinates[],
  pattern2: Coordinates[]
) => boolean = (pattern1, pattern2) =>
  pattern1.every((c1) => pattern2.some((c2) => sameCoordinates(c1, c2)));

const getPatternsMatchingSelection: (
  blueprint: Blueprint,
  selection: Coordinates[]
) => Coordinates[][] = (blueprint, selection) =>
  getAllBlueprintCoordinatesWithTranslation(blueprint, selection[0]).filter(
    (pattern) => samePattern(selection, pattern)
  );

export const validateExpansionBlueprint: (
  map: TerrainMap,
  pattern: Coordinates[],
  blueprint: Blueprint,
  player: Player
) => boolean = (map, pattern, blueprint, player) =>
  getPatternsMatchingSelection(blueprint, pattern).some(
    (selection) =>
      selection.every(isWithinMap) &&
      isFreeTerrainOnly(selection, map) &&
      (isSelectionAdjacentToCoordinates(selection, player.swarm) ||
        coordinatesOverlap(selection, startCorners()))
  );

export const validateBuildBlueprint: (
  map: TerrainMap,
  pattern: Coordinates[],
  blueprint: Blueprint,
  player: Player
) => boolean = (map, pattern, blueprint, player) =>
  getPatternsMatchingSelection(blueprint, pattern).some(
    (selection) =>
      containsCoords(player.swarm, selection) &&
      getAdjacentToSelection(selection).every((c) =>
        occupiedByCritters(c, map, player.id)
      )
  );

export const validateBuildingBlueprint: (
  map: TerrainMap,
  pattern: Coordinates[],
  blueprint: Blueprint,
  player: Player
) => boolean = (_, pattern, blueprint, player) =>
  getPatternsMatchingSelection(blueprint, pattern).some(
    (selection) =>
      selection.every(isWithinMap) && containsCoords(player.city, selection)
  );

const patternsAreAligned = (pattern1: Coordinates[], pattern2: Coordinates[]) =>
  [
    ...Object.values(
      subtractCoordinates(
        getBoundingBox(pattern1).topLeft,
        getBoundingBox(pattern2).topLeft
      )
    ),
  ].some((diff) => diff === 0);

export const validateDeliveryBlueprint: (
  map: TerrainMap,
  blueprint: Blueprint,
  location: Coordinates[],
  selection: Coordinates[],
  id: number
) => boolean = (map, blueprint, location, selection, id) =>
  getPatternsMatchingSelection(blueprint, selection).some(
    (selection) =>
      selection.every(isWithinMap) &&
      isFreeTerrainOnly(selection, map) &&
      patternsAreAligned(location, selection) &&
      isAdjacentOpponentOwnedTerrains(selection, id, map)
  );

const isAdjacentOpponentOwnedTerrains = (
  selection: Coordinates[],
  id: number,
  map: TerrainMap
) =>
  getAdjacentToSelection(selection)
    .map((c) => map[c.row][c.col])
    .some((t) => t.state === "occupied" && t.owner !== id);

const isSelectionAdjacentToCoordinates = (
  selection: Coordinates[],
  swarm: Coordinates[]
) => selection.some((c1) => swarm.some((c2) => isAdjacentTo(c1, c2)));

const coordinatesOverlap = (selection: Coordinates[], corners: Coordinates[]) =>
  selection.some((c1) => corners.some((c2) => sameCoordinates(c1, c2)));

const isFreeTerrainOnly = (selection: Coordinates[], map: TerrainMap) =>
  selection.every((c) => map[c.row][c.col].state === "free");

const occupiedByCritters = (coord: Coordinates, map: TerrainMap, id: number) =>
  ((terrain: Terrain) => terrain.state === "occupied" && terrain.owner === id)(
    map[coord.row][coord.col]
  );
