import type { ActionType } from "./ActionType";
import { ActionTypes } from "./ActionType";
import type { Action } from "./Action";
import type { Blueprint } from "./Blueprint";
import { emptyBag, resourceFromActionType } from "./Resource";
import { numberlist } from "./utils";

export const GRID_SIZE = 4;

type GridRow = {
  [k in ActionType]: Action<ActionType>;
};

const emptyGridRow: (rowNumber: number) => GridRow = (rowNumber) =>
  ActionTypes.reduce(
    (row: GridRow, actionType, colNumber) => ({
      ...row,
      [actionType]: {
        actionType,
        row: rowNumber,
        blueprint: undefined,
        value: 1 + Math.trunc((rowNumber + colNumber) / 2),
      } as Action<typeof actionType>,
    }),
    {} as GridRow
  );

export type TechGrid = GridRow[];

export const playerStartingGrid = () =>
  numberlist(GRID_SIZE).map((i) => emptyGridRow(i));

const isInitialPosition = (
  grid: TechGrid,
  row: number,
  actionType: ActionType
) =>
  row === 0 &&
  actionType === ActionTypes[0] &&
  grid[row][actionType].blueprint === undefined;

const isAdjacentToExistingUpgrade = (
  grid: TechGrid,
  row: number,
  col: number
) =>
  [
    ...(row === 0 ? [] : [grid[row - 1][ActionTypes[col]]]),
    ...(row === GRID_SIZE - 1 ? [] : [grid[row + 1][ActionTypes[col]]]),
    ...(col === 0 ? [] : [grid[row][ActionTypes[col - 1]]]),
    ...(col === ActionTypes.length - 1
      ? []
      : [grid[row][ActionTypes[col + 1]]]),
  ].some((s) => s.blueprint !== undefined);

export const isUniqueBlueprint = (
  grid: TechGrid,
  row: number,
  actionType: ActionType,
  blueprint: Blueprint
) =>
  grid[row][actionType].blueprint === blueprint ||
  (grid.every((row) => row[actionType].blueprint !== blueprint) &&
    ActionTypes.every((a) => grid[row][a].blueprint !== blueprint));

export const isValidSpaceSelection = (
  grid: TechGrid,
  row: number,
  actionType: ActionType,
  blueprint: Blueprint
) => {
  const col = ActionTypes.findIndex((a) => a === actionType);

  const previouslyUpgradedAction =
    grid[row][actionType].blueprint === blueprint;
  if (previouslyUpgradedAction) {
    return actionType !== "science";
  }
  const upgradeRequirements =
    grid[row][actionType].blueprint === undefined &&
    isUniqueBlueprint(grid, row, actionType, blueprint);
  const adjacencyRequirement =
    isInitialPosition(grid, row, actionType) ||
    isAdjacentToExistingUpgrade(grid, row, col);
  return upgradeRequirements && adjacencyRequirement;
};

export const blueprintMastery: (
  grid: TechGrid,
  blueprint?: Blueprint
) => number = (grid, blueprint) =>
  blueprint === undefined
    ? 0
    : grid
        .flatMap((row) => ActionTypes.map((actionType) => row[actionType]))
        .filter((action) => action.blueprint === blueprint).length;

export const actionCost = (action: Action<ActionType>, amount: number = 0) => ({
  ...emptyBag,
  [resourceFromActionType(action.actionType)]: action.value + amount,
});

export function upgradeGrid(
  grid: TechGrid,
  action: Action<ActionType>,
  token: Blueprint
): TechGrid {
  if (token === undefined) {
    return grid;
  } else if (action === undefined) {
    return grid;
  } else
    return grid.map((originalRow, i) =>
      ActionTypes.reduce(
        (newRow, actionType) => ({
          ...newRow,
          [actionType]:
            i === action.row && actionType === action.actionType
              ? {
                  ...originalRow[actionType],
                  blueprint: token,
                }
              : originalRow[actionType],
        }),
        originalRow
      )
    );
}
