import type { Game, Move } from "boardgame.io";
import { INVALID_MOVE } from "boardgame.io/core";

import { deepAssign, numberlist } from "./utils";

import type { Blueprint } from "./Blueprint";
import {
  getAllBlueprintCoordinatesWithTranslation,
  validateBuildBlueprint,
  validateDeliveryBlueprint,
  validateExpansionBlueprint,
  validateBuildingBlueprint,
} from "./Blueprint";
import { actionCost, isValidSpaceSelection, upgradeGrid } from "./Grid";
import type { Coordinates, TerrainMap } from "./Map";
import {
  addCoordinates,
  GAME_SIZE,
  getAdjacentToSelection,
  containsCoord,
  newMap,
  raiseMap,
  sameCoordinates,
  startCorners,
  VECTOR_ORIENTATIONS,
  applyMapEffect,
} from "./Map";
import type { Market } from "./Market";
import { discardMarketToken, newMarket, refillMarketTokens } from "./Market";
import type { Player } from "./Player";
import { newPlayer, nextPlayer, winner } from "./Player";
import {
  addBags,
  resourcesFromMap,
  payWithCoins,
  actionTime,
} from "./Resource";
import type { Track } from "./Track";
import { moveOnTurnTrack } from "./Track";
import type { ActionType, ActionSelection } from "./ActionType";
import { ActionTypes, nextState, noAction } from "./ActionType";
import { getActionFromType, scienceScore, upgrade } from "./Action";

const url = new URL(window.location.href);
const [appName, readMatchID, readPlayerID] = url.pathname.split("/");
const matchID = readMatchID
  ? readMatchID
  : Math.trunc(Math.random() * 1000000000000).toString();

url.pathname = [appName, matchID, readPlayerID ? readPlayerID : "0"].join("/");
window.history.replaceState(null, "swarms!", url);

export const NUM_PLAYERS = 4;

const GAME_LENGTH = 3; // 2 for a shorter game

export type Shuffle = <T>(deck: T[]) => T[];

export type Random = () => number;

export type SwarmsState = {
  map: TerrainMap;
  market: Market;
  players: Player[];
  turnTrack: Track;
  gameEndTriggered?: number;
};

const playerSetupSwarm: (
  G: SwarmsState,
  token: Blueprint,
  id: number
) => SwarmsState = (G, token, id) => {
  const currentPlayer = G.players[id];
  const grid = upgradeGrid(
    currentPlayer.grid,
    currentPlayer.grid[0][ActionTypes[0]],
    token
  );
  const moves = getExpansionPatterns(G.map, token, currentPlayer);
  const player = { ...currentPlayer, grid, swarm: moves[0] };
  const map = applyMapEffect(G.map, moves[0], { owner: id, state: "occupied" });
  return deepAssign({ ...G, map }, player, "players", (p) => p.id === id);
};

const playerSetupState: (G: SwarmsState) => SwarmsState = (G) =>
  G.players.reduce(
    (acc, _, i) => playerSetupSwarm(acc, G.players[i].blueprint, i),
    G
  );

const triggerGameEnd = (G: SwarmsState, id: number) =>
  G.gameEndTriggered === undefined &&
  nextPlayer(G.players, G.turnTrack) !== id &&
  G.players[id].grid.filter((row) => row.science.blueprint !== undefined)
    .length >= GAME_LENGTH;

export const passTime: (G: SwarmsState) => SwarmsState | undefined = (G) => {
  const currentPlayer = G.players.find(
    (p) => p.actionSelection.state !== "inactive"
  );
  if (currentPlayer === undefined) {
    return G;
  }
  const actionCoins = payWithCoins(
    currentPlayer.actionSelection.cost.resources,
    resourcesFromMap(G.map, currentPlayer.swarm)
  );
  const timeTaken = actionTime(
    currentPlayer.actionSelection,
    resourcesFromMap(G.map, currentPlayer.swarm)
  );
  const timeSpent = currentPlayer.timeSpent + timeTaken;
  const turnTrack = moveOnTurnTrack(
    G.turnTrack,
    currentPlayer.timeSpent,
    timeSpent
  );
  const coinsSpent = currentPlayer.coinsSpent + actionCoins;
  if (coinsSpent > currentPlayer.coins) {
    return undefined;
  }

  const nextG = {
    ...G,
    players: G.players.map((p) =>
      p.id === currentPlayer.id ? { ...p, timeSpent, coinsSpent } : p
    ),
    turnTrack,
  };

  if (triggerGameEnd(nextG, currentPlayer.id)) {
    return { ...nextG, gameEndTriggered: timeSpent };
  }
  return nextG;
};

const refillMarket: Move<SwarmsState> = ({ G, playerID, random }) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (currentPlayer.actionSelection.action !== undefined) {
    return invalidMove();
  }
  const market = refillMarketTokens(G.market, random.Shuffle);
  return { ...G, market };
};

const discardToken: Move<SwarmsState> = ({ G, playerID, events }) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (G.market.stalls.length === 0) {
    return invalidMove();
  }
  const market = discardMarketToken(G.market);
  const time = currentPlayer.actionSelection.cost.time + 1;
  const discardGame = passTime(
    deepAssign(
      { ...resetAction(G, id), market },
      time,
      "players",
      (p) => p.id === id,
      "actionSelection",
      "cost",
      "time"
    )
  );
  if (discardGame === undefined) return invalidMove();
  events.endTurn();
  return discardGame;
};

const selectLocation: Move<SwarmsState> = (
  { G, playerID },
  coords: Coordinates[],
  actionType: ActionType
) => {
  const token = G.market.stalls[0];
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (containsCoord(currentPlayer.actionSelection.location, coords[0])) {
    return deepAssign(
      G,
      currentPlayer.actionSelection.location.filter(
        (c) => !sameCoordinates(c, coords[0])
      ),
      "players",
      (p) => p.id === id,
      "actionSelection",
      "location"
    );
  }
  const location = [...currentPlayer.actionSelection.location, ...coords];
  const actionSelection = {
    ...currentPlayer.actionSelection,
    location,
  };
  const validation =
    actionType === "swarm"
      ? validateExpansionBlueprint
      : actionType === "city"
      ? validateBuildBlueprint
      : actionType === "trade"
      ? validateBuildingBlueprint
      : actionType === "science"
      ? validateBuildingBlueprint
      : () => false;
  const valid = validation(
    G.map,
    actionSelection.location,
    token,
    currentPlayer
  );
  if (valid) {
    if (location.length === 4) {
      return nextGameState(G, id, actionSelection);
    }
    return deepAssign(
      G,
      actionSelection,
      "players",
      (p) => p.id === id,
      "actionSelection"
    );
  }
  return invalidMove();
};

const selectDestination: Move<SwarmsState> = (
  { G, playerID },
  coords: Coordinates[]
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (containsCoord(currentPlayer.actionSelection.location, coords[0])) {
    return invalidMove();
  }
  if (containsCoord(currentPlayer.actionSelection.destination, coords[0])) {
    return deepAssign(
      G,
      currentPlayer.actionSelection.destination.filter(
        (c) => !sameCoordinates(c, coords[0])
      ),
      "players",
      (p) => p.id === id,
      "actionSelection",
      "destination"
    );
  }
  const destination = [...currentPlayer.actionSelection.destination, ...coords];
  if (
    validateDeliveryBlueprint(
      G.map,
      G.market.stalls[0],
      currentPlayer.actionSelection.location,
      destination,
      id
    )
  ) {
    const actionSelection = {
      ...currentPlayer.actionSelection,
      destination,
    };
    return deepAssign(
      G,
      actionSelection,
      "players",
      (p) => p.id === id,
      "actionSelection"
    );
  }
  return invalidMove();
};

const nextGameState: (
  G: SwarmsState,
  id: number,
  actionSelection: ActionSelection<ActionType>
) => SwarmsState = (G, id, actionSelection) =>
  deepAssign(
    G,
    { ...actionSelection, state: nextState(actionSelection.state) },
    "players",
    (p) => p.id === id,
    "actionSelection"
  );

const debugRaiseMap: Move<SwarmsState> = ({ G, random }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const raisedTerrains = raiseMap(originalTerrains, random.Number);
  const improvedMap = raisedTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const selectAction: Move<SwarmsState> = (
  { G, playerID },
  row: number,
  actionType: ActionType
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const token = G.market.stalls[0];
  if (currentPlayer.actionSelection.state !== "market" || token === undefined) {
    return invalidMove();
  }
  const action = currentPlayer.grid[row][actionType];
  if (isValidSpaceSelection(currentPlayer.grid, row, actionType, token)) {
    const cost = actionCost(action);
    const actionSelection = {
      ...currentPlayer.actionSelection,
      action,
      cost: {
        ...currentPlayer.actionSelection.cost,
        resources: addBags(currentPlayer.actionSelection.cost.resources, cost),
      },
    };
    return nextGameState(G, id, actionSelection);
  }
  return invalidMove();
};

const confirmAction: Move<SwarmsState> = ({ G, playerID, events }) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (currentPlayer.actionSelection.state !== "destination") {
    return invalidMove();
  }
  if (currentPlayer.actionSelection.action === undefined) {
    return invalidMove();
  }
  if (
    currentPlayer.actionSelection.action.actionType === "trade" &&
    currentPlayer.actionSelection.destination.length < 4
  ) {
    return invalidMove();
  }
  const action = getActionFromType(
    currentPlayer.actionSelection.action.actionType
  );
  const actionGame = passTime(action(upgrade(G, id), id));
  if (actionGame === undefined) return invalidMove();
  events.endTurn();
  return actionGame;
};

const resetAction = (G: SwarmsState, id: number) =>
  deepAssign(
    G,
    { ...noAction, state: "market" },
    "players",
    (p) => p.id === id,
    "actionSelection"
  );

const discardAction: Move<SwarmsState> = ({ G, playerID }) =>
  resetAction(G, parseInt(playerID || "0"));

const getExpansionPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  getAdjacentToSelection(
    player.swarm.length > 0 ? player.swarm : [startCorners()[player.id]]
  ).flatMap((c) =>
    getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
      (bp) =>
        bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
        validateExpansionBlueprint(map, bp, blueprint, player)
    )
  );

const getBuildPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.swarm.flatMap((c) =>
    getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
      (bp) =>
        bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
        validateBuildBlueprint(map, bp, blueprint, player)
    )
  );

const getBuildingPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.city.flatMap((c) =>
    getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
      (bp) =>
        bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
        validateBuildingBlueprint(map, bp, blueprint, player)
    )
  );

const getBlueprintDestinationDrop: (
  map: TerrainMap,
  location: Coordinates[],
  moveUnit: Coordinates,
  blueprint: Blueprint,
  id: number
) => Coordinates[] | undefined = (map, location, moveUnit, blueprint, id) =>
  numberlist(GAME_SIZE).reduce(
    (acc: { last: Coordinates[]; furthest: Coordinates[] | undefined }, _) => {
      const next = acc.last.map((c: Coordinates) =>
        addCoordinates(c, moveUnit)
      );
      return {
        last: next,
        furthest: validateDeliveryBlueprint(map, blueprint, location, next, id)
          ? next
          : acc.furthest,
      };
    },
    { last: location, furthest: undefined }
  ).furthest;

export const Swarms: Game<SwarmsState> = {
  seed: matchID,

  setup: ({ random, ctx }) => {
    const map: TerrainMap = newMap(random.Number);

    const playerOrder = numberlist(ctx.numPlayers);
    const players: Player[] = playerOrder.map((id) =>
      newPlayer(id, startCorners()[id])
    );
    const turnTrack: Track = [[...playerOrder]];
    const market = newMarket;
    const gameEndTriggered = undefined;

    const initialPlayerGame = {
      map,
      market,
      players,
      turnTrack,
      gameEndTriggered,
    };

    return playerSetupState(initialPlayerGame);
  },

  turn: {
    onBegin: ({ G, ctx, random }) => {
      const market =
        G.market.stalls.length === 0
          ? refillMarketTokens(G.market, random.Shuffle)
          : G.market;
      const id = parseInt(ctx.currentPlayer);
      return nextGameState({ ...G, market }, id, G.players[id].actionSelection);
    },
    order: {
      first: () => 0,
      next: ({ G }) => nextPlayer(G.players, G.turnTrack, G.gameEndTriggered),
    },
    onEnd: ({ G }) =>
      deepAssign(G, noAction, "players", () => true, "actionSelection"),
  },

  moves: {
    refillMarket,
    discardToken,
    selectAction,
    selectLocation,
    selectDestination,
    confirmAction,
    discardAction,
    debugRaiseMap,
  },

  endIf: ({ G: { players, gameEndTriggered } }) => {
    if (gameEndTriggered !== undefined) {
      if (players.every((p) => p.timeSpent >= gameEndTriggered)) {
        return winner(players);
      }
    }
  },

  onEnd: ({ G }) => {
    const players = G.players.map((player) => {
      const coins = scienceScore(player).reduce(
        (acc, score, i) => acc + score * player.grid[i].science.value,
        player.coins
      );
      return { ...player, coins };
    });
    console.log({
      "player scores": players.map((p) => p.coins - p.coinsSpent),
      "time consumed": G.gameEndTriggered,
    });
    return { ...G, players };
  },

  ai: {
    enumerate: (G, _ctx, playerID) => {
      const id = parseInt(playerID || "0");
      const currentPlayer = G.players[id];
      if (G.market.stalls.length === 0) {
        return [{ move: "refillMarket" }];
      }
      const token = G.market.stalls[0];
      switch (currentPlayer.actionSelection.state) {
        case "inactive":
          return [];
        case "market": {
          const moves = currentPlayer.grid.flatMap((row, i) =>
            ActionTypes.filter(
              (actionType) =>
                isValidSpaceSelection(
                  currentPlayer.grid,
                  i,
                  actionType,
                  token
                ) &&
                currentPlayer.coins - currentPlayer.coinsSpent >=
                  payWithCoins(
                    actionCost(row[actionType]),
                    resourcesFromMap(G.map, currentPlayer.swarm)
                  )
            ).map((actionType) => ({
              move: "selectAction",
              args: [i, actionType],
            }))
          );
          if (moves.length === 0) {
            return [{ move: "discardToken" }];
          }
          return moves;
        }
        case "location": {
          if (currentPlayer.actionSelection.action === undefined) {
            return [{ move: "discardAction" }];
          }
          switch (currentPlayer.actionSelection.action.actionType) {
            case "swarm": {
              const moves = getExpansionPatterns(
                G.map,
                token,
                currentPlayer
              ).map((pattern) => ({
                move: "selectLocation",
                args: [pattern, "swarm"],
              }));
              if (moves.length === 0) {
                return [{ move: "discardToken" }];
              }
              return moves;
            }
            case "city": {
              const moves = getBuildPatterns(G.map, token, currentPlayer).map(
                (pattern) => ({
                  move: "selectLocation",
                  args: [pattern, "city"],
                })
              );
              if (moves.length === 0) {
                return [{ move: "discardToken" }];
              }
              return moves;
            }
            case "trade": {
              const moves = getBuildingPatterns(G.map, token, currentPlayer)
                .filter(
                  (pattern) =>
                    VECTOR_ORIENTATIONS.map((moveUnit) =>
                      getBlueprintDestinationDrop(
                        G.map,
                        pattern,
                        moveUnit,
                        token,
                        id
                      )
                    ).filter((pattern) => pattern !== undefined).length > 0
                )
                .map((pattern) => ({
                  move: "selectLocation",
                  args: [pattern, "trade"],
                }));
              if (moves.length === 0) {
                return [{ move: "discardToken" }];
              }
              return moves;
            }
            case "science":
              const moves = getBuildingPatterns(
                G.map,
                token,
                currentPlayer
              ).map((pattern) => ({
                move: "selectLocation",
                args: [pattern, "science"],
              }));
              if (moves.length === 0) {
                return [{ move: "discardToken" }];
              }
              return moves;
          }
        }
        case "destination": {
          if (currentPlayer.actionSelection.action === undefined) {
            return [{ move: "discardAction" }];
          }
          if (
            currentPlayer.actionSelection.action.actionType === "trade" &&
            currentPlayer.actionSelection.destination.length !== 4
          ) {
            const moves = VECTOR_ORIENTATIONS.map((moveUnit) =>
              getBlueprintDestinationDrop(
                G.map,
                currentPlayer.actionSelection.location,
                moveUnit,
                token,
                id
              )
            )
              .filter((pattern) => pattern !== undefined)
              .map((pattern) => ({
                move: "selectDestination",
                args: [pattern],
              }));
            if (moves.length === 0) {
              return [{ move: "discardToken" }];
            }
            return moves;
          } else {
            return [{ move: "confirmAction" }];
          }
        }
      }
    },
  },
};

const invalidMove = () => {
  const a = true || false;
  if (a) return INVALID_MOVE;
};
