import type { BoardProps } from "boardgame.io/react";
import type { Action } from "./Action";
import { scienceScore } from "./Action";
import { Blueprints, type Blueprint } from "./Blueprint";
import type { Coordinates, TerrainMap } from "./Map";
import {
  addCoordinates,
  GAME_SIZE,
  sameCoordinates,
  scaleCoordinates,
  subtractCoordinates,
} from "./Map";
import type { Market } from "./Market";
import { winner, type Player } from "./Player";
import type { Resource } from "./Resource";
import {
  resourcesFromMap,
  resourceFromActionType,
  Resources,
  actionTime,
} from "./Resource";
import type { Species } from "./Species";
import { SpeciesList } from "./Species";
import { NUM_PLAYERS, type SwarmsState } from "./Swarms";
import { terrainFromResource, type Terrain, type TerrainType } from "./Terrain";
import { numberlist } from "./utils";
import type { ActionType, ActionSelection } from "./ActionType";
import { ActionTypes } from "./ActionType";
import { Fragment } from "react";

const TIME_EMOJI = "";

const interfaceSpacing = 1;
const cellSize = (105 - interfaceSpacing * 4) / GAME_SIZE;
const fontSize = cellSize / 2.5;
const terrainBorder = 0.2;
const terrainStyle: React.CSSProperties = {
  border: `${terrainBorder}vmin solid white`,
  margin: "0vmin",
  padding: "0vmin",
  width: `${cellSize - 2 * terrainBorder}vmin`,
  height: `${cellSize - 2 * terrainBorder}vmin`,
  lineHeight: `${cellSize - 2 * terrainBorder}vmin`,
  textAlign: "center",
  cursor: "pointer",
};

const colorFromTerrain = (type: TerrainType) => {
  switch (type) {
    case "water":
      return "lightblue";
    case "forest":
      return "lightgreen";
    case "desert":
      return "khaki";
    case "mountain":
      return "lightcoral";
    case "city":
      return "white";
  }
};

const emojiFromResource = (type: Resource) => {
  switch (type) {
    case "food":
      return "";
    case "log":
      return "";
    case "energy":
      return "";
    case "ore":
      return "";
    case "coin":
      return "";
  }
};

const emojiFromSpecies = (species: Species) => {
  switch (species) {
    case "fish":
      return "";
    case "bird":
      return "";
    case "scarab":
      return "";
    case "fungi":
      return "";
  }
};

const emojiFromBuilding = (blueprint: Blueprint) => {
  switch (blueprint) {
    case "S":
      return "";
    case "O":
      return "";
    case "T":
      return "";
    case "I":
      return "";
    case "L":
      return "";
  }
};

const researchOrder = ["", "", "", ""];

interface GameProps extends BoardProps<SwarmsState> {}

export function Board(props: GameProps) {
  const displayPlayer = props.G.players[parseInt(props.playerID || "0")];

  type ActionProps = {
    action: Action<ActionType>;
    selected: boolean;
    species: Species;
  };

  interface BlueprintProps {
    blueprint?: Blueprint;
    selected: boolean;
  }

  const BlueprintGrid = ({ blueprint, selected }: BlueprintProps) => (
    <svg
      height="10vmin"
      width="12vmin"
      preserveAspectRatio="xMinYMin slice"
      viewBox="0 0 10 12"
    >
      <BlueprintGridPips
        coord={{ row: 4, col: 5 }}
        blueprint={blueprint}
        size={8}
        selected={selected}
      />
    </svg>
  );

  const Action = ({ action, selected, species }: ActionProps) => (
    <>
      <div>
        <BlueprintGrid blueprint={action.blueprint} selected={selected} />
      </div>
      <div>
        <span style={{ float: "left" }}>
          &nbsp;
          {action.actionType === "science"
            ? `${scienceScore(displayPlayer)[action.row]}${
                researchOrder[action.row]
              }`
            : action.actionType === "swarm" && action.value === 0
            ? emojiFromSpecies(species)
            : null}
        </span>
        <span style={{ float: "right" }}>
          {action.value}
          &nbsp;
        </span>
      </div>
    </>
  );

  interface MarketStallProps {
    token: Blueprint;
  }

  const Token = ({ token }: MarketStallProps) =>
    token !== undefined ? (
      <svg
        height="6vmin"
        width="6vmin"
        preserveAspectRatio="xMinYMin slice"
        viewBox="0 0 10 10"
      >
        <BlueprintToken coord={{ row: 0, col: 0 }} blueprint={token} />
      </svg>
    ) : null;

  const resolveTerrain = (coord: Coordinates) => {
    const id = parseInt(props.playerID || "0");
    const currentPlayer = props.G.players[id];
    if (currentPlayer.actionSelection.action === undefined) return;
    switch (currentPlayer.actionSelection.state) {
      case "location":
        props.moves.selectLocation(
          [coord],
          currentPlayer.actionSelection.action.actionType
        );
        break;
      case "destination":
        props.moves.selectDestination([coord]);
        break;
    }
  };

  interface MarketProps {
    market: Market;
  }

  const refillMarket = () => {
    props.moves.refillMarket();
  };

  const discardToken = () => {
    props.moves.discardToken();
  };

  const Market = ({ market }: MarketProps) => (
    <table
      style={{
        fontSize: `${fontSize}vmin`,
      }}
    >
      <thead>
        <tr>
          <th key="market" colSpan={2}>
            market
          </th>
        </tr>
      </thead>
      <tbody>
        <tr key="stalls">
          <td key="stallcost" />
          <td
            key="stallhead"
            style={{
              verticalAlign: "top",
              lineHeight: "6vmin",
              textAlign: "right",
            }}
          >
            stalls
          </td>
          <td colSpan={5} key="tokens">
            {market.stalls.map((token, i) => (
              <Token key={i} token={token} />
            ))}
          </td>
        </tr>
        <tr key="discard" style={{ cursor: "pointer" }} onClick={discardToken}>
          <td
            key="discardcost"
            style={{
              verticalAlign: "top",
              lineHeight: "6vmin",
              textAlign: "right",
            }}
          >
            {TIME_EMOJI}
          </td>
          <td
            key="discardhead"
            style={{
              verticalAlign: "top",
              lineHeight: "6vmin",
              textAlign: "right",
            }}
          >
            discard
          </td>
          <td key="dtokens">
            {market.discard.map((token, i) => (
              <Fragment key={i}>
                <Token token={token} />
                {i % 10 === 9 ? <br /> : null}
              </Fragment>
            ))}
          </td>
        </tr>
        <tr key="bag" style={{ cursor: "pointer" }} onClick={refillMarket}>
          <td key="bagcost" />
          <td
            key="baghead"
            style={{
              verticalAlign: "top",
              lineHeight: "6vmin",
              textAlign: "right",
            }}
          >
            bag
          </td>
          <td key="btokens">
            {market.bag.map((token, i) => (
              <Fragment key={i}>
                <Token token={token} />
                {i % 10 === 9 ? <br /> : null}
              </Fragment>
            ))}
          </td>
        </tr>
      </tbody>
    </table>
  );

  interface PlayerProps {
    player: Player;
  }

  const COINS_LINE = 20;
  const COINS_LINE_GROUP = 5;

  const Player = ({ player }: PlayerProps) => (
    <table
      key={`player${player.id}`}
      style={{
        fontSize: `${fontSize}vmin`,
      }}
    >
      <thead>
        <tr>
          {ActionTypes.map((actionType, i) => (
            <th key={i} style={{ textAlign: "right" }}>
              {`${actionType} ${emojiFromResource(
                resourceFromActionType(actionType)
              )}`}
            </th>
          ))}
          <th key="coins">
            <span style={{ fontWeight: "normal" }}>
              coins {player.coins - player.coinsSpent}
              {props.ctx.gameover && winner(props.G.players).id === player.id
                ? ""
                : null}
            </span>
          </th>
        </tr>
      </thead>
      <tbody>
        {player.grid.map((row, i) => (
          <tr key={`gridrow${i}`}>
            {ActionTypes.map((actionType, col) => (
              <td
                key={`${i}x${col}`}
                style={{
                  border: "0.1vmin solid #555",
                  backgroundColor: colorFromTerrain(
                    terrainFromResource(resourceFromActionType(actionType))
                  ),
                  cursor: "pointer",
                }}
                onClick={() => props.moves.selectAction(i, actionType)}
              >
                <Action
                  action={row[actionType]}
                  selected={
                    i === player.actionSelection.action?.row &&
                    actionType === player.actionSelection.action.actionType
                  }
                  species={player.species}
                />
              </td>
            ))}
            <td key={`coins${i}`}>
              {numberlist(COINS_LINE_GROUP).map((line) => (
                <span
                  key={`${i}x${line}`}
                  style={{ fontSize: `${fontSize - 0.25}vmin` }}
                >
                  {numberlist(COINS_LINE).map((linecoin) => {
                    const coin =
                      i * COINS_LINE_GROUP * COINS_LINE +
                      line * COINS_LINE +
                      linecoin;
                    return coin < player.coinsSpent
                      ? ""
                      : coin < player.coins
                      ? ""
                      : "";
                  })}
                  <br />
                </span>
              ))}
            </td>
          </tr>
        ))}
        <tr key="playertime">
          <td colSpan={4}>
            <span style={{ float: "left" }}>
              {TIME_EMOJI} time spent: {player.timeSpent}
            </span>
            {player.actionSelection.state !== "inactive" ? (
              <span
                style={{ cursor: "pointer", float: "right" }}
                onClick={() => props.moves.confirmAction()}
              >
                 confirm action
              </span>
            ) : (
              <span style={{ float: "right" }}>wait for your turn</span>
            )}
          </td>
        </tr>
        <tr key="actiontime">
          <td colSpan={4}>
            <span style={{ float: "left" }}>
              {TIME_EMOJI} current action:&nbsp;
              {actionTime(
                player.actionSelection,
                resourcesFromMap(props.G.map, player.swarm)
              )}
            </span>
            {player.actionSelection.state !== "inactive" ? (
              <span
                style={{ cursor: "pointer", float: "right" }}
                onClick={() => props.moves.discardAction()}
              >
                 discard action
              </span>
            ) : null}
          </td>
        </tr>
        <tr key="requiredresources">
          <td colSpan={4}>
            required resources:
            {Resources.map(
              (r) =>
                ` ${emojiFromResource(r)}: ${
                  player.actionSelection.cost.resources[r]
                }`
            )}
          </td>
        </tr>
        <tr key="providedresources">
          <td colSpan={4}>
            provided resources:
            {Resources.map(
              (r) =>
                ` ${emojiFromResource(r)}: ${
                  resourcesFromMap(props.G.map, player.swarm)[r]
                }`
            )}
          </td>
        </tr>
      </tbody>
    </table>
  );

  interface TerrainProps {
    terrain: Terrain;
    actionSelection: ActionSelection<ActionType>;
  }

  const Terrain = ({ terrain, actionSelection }: TerrainProps) => {
    const blueprint = actionSelection.location.some((coord) =>
      sameCoordinates(coord, terrain.coord)
    );
    const destination = actionSelection.destination.some((coord) =>
      sameCoordinates(coord, terrain.coord)
    );
    const content =
      terrain.state === "built"
        ? emojiFromBuilding(terrain.blueprint)
        : terrain.state === "occupied"
        ? emojiFromSpecies(SpeciesList[terrain.owner])
        : "";
    return (
      <>{destination ? `<${content}>` : blueprint ? `[${content}]` : content}</>
    );
  };

  interface MapProps {
    map: TerrainMap;
    actionSelection: ActionSelection<ActionType>;
  }

  const Map = ({ map, actionSelection }: MapProps) => (
    <table
      key="sheet"
      style={{
        borderSpacing: "0vmin",
        emptyCells: "show",
      }}
    >
      <tbody>
        {map.map((row, i) => (
          <tr key={i}>
            {row.map((terrain, j) => (
              <td
                key={j}
                style={{
                  ...terrainStyle,
                  fontSize: `${fontSize}vmin`,
                  backgroundColor: colorFromTerrain(
                    terrain.state === "built" ? "city" : terrain.type
                  ),
                }}
                onClick={() => resolveTerrain({ row: i, col: j })}
              >
                <Terrain terrain={terrain} actionSelection={actionSelection} />
              </td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );

  interface PipProps {
    coord: Coordinates;
    color: string;
    selected?: boolean;
  }

  const Pip = ({ coord: { row, col }, color, selected = true }: PipProps) => (
    <rect
      x={col}
      y={row}
      width={PIP_SIZE}
      height={PIP_SIZE}
      rx={PIP_SIZE / 5}
      fill={color}
      fillOpacity={selected ? 1 : 0}
      stroke="black"
      strokeWidth={PIP_SIZE / 50}
    />
  );

  const TOKEN_SIZE = 9.5;
  const PIP_SIZE = TOKEN_SIZE / 5;
  const PADDING = 0.5;
  const CONTROL_LINE = 0.02;

  interface BlueprintPipsProps {
    blueprint?: Blueprint;
    coord: Coordinates;
    size: number;
    selected?: boolean;
  }

  const BlueprintTokenPips = ({
    blueprint,
    coord,
    size,
  }: BlueprintPipsProps) => (
    <>
      {blueprintPipsCoords(blueprint).map((pipCoord, i) => (
        <Pip
          key={i}
          coord={addCoordinates(
            addCoordinates({ row: size / 2, col: size / 2 }, coord),
            pipCoord.coord
          )}
          color={blueprintColor(blueprint)}
        />
      ))}
    </>
  );

  const BlueprintGridPips = ({
    blueprint,
    coord,
    selected,
  }: BlueprintPipsProps) => (
    <>
      {blueprintPipsCoords().map((pipGrid, i) => {
        const filledPip =
          blueprint !== undefined &&
          blueprintPips(blueprint).some((pip) =>
            sameCoordinates(pip, pipGrid.pip)
          );
        return (
          <Pip
            key={i}
            coord={addCoordinates(coord, pipGrid.coord)}
            color={filledPip ? blueprintColor(blueprint) : blueprintColor()}
            selected={filledPip || !selected}
          />
        );
      })}
    </>
  );

  interface BaseBlueprintProps {
    coord: Coordinates;
    blueprint?: Blueprint;
  }

  const BlueprintToken = ({ coord, blueprint }: BaseBlueprintProps) => (
    <>
      <circle
        cx={coord.col + TOKEN_SIZE / 2}
        cy={coord.row + TOKEN_SIZE / 2}
        r={TOKEN_SIZE / 2}
        fill="white"
        stroke="black"
        strokeWidth={CONTROL_LINE}
      />
      <BlueprintTokenPips
        blueprint={blueprint}
        coord={coord}
        size={TOKEN_SIZE}
      />
    </>
  );

  const blueprintColor = (blueprint?: Blueprint) =>
    blueprint
      ? {
          O: "yellow",
          I: "cyan",
          T: "purple",
          S: "green",
          L: "orange",
          Z: "red",
          J: "blue",
        }[blueprint]
      : "white";

  const blueprintPips: (blueprint?: Blueprint) => Coordinates[] = (blueprint) =>
    blueprint
      ? {
          O: [
            { row: 0, col: 0 },
            { row: 0, col: 1 },
            { row: 1, col: 0 },
            { row: 1, col: 1 },
          ],
          I: [
            { row: 0, col: 0 },
            { row: 0, col: 1 },
            { row: 0, col: 2 },
            { row: 0, col: 3 },
          ],
          T: [
            { row: 0, col: 0 },
            { row: 0, col: 1 },
            { row: 0, col: 2 },
            { row: 1, col: 1 },
          ],
          S: [
            { row: 0, col: 1 },
            { row: 0, col: 2 },
            { row: 1, col: 0 },
            { row: 1, col: 1 },
          ],
          L: [
            { row: 0, col: 0 },
            { row: 0, col: 1 },
            { row: 0, col: 2 },
            { row: 1, col: 0 },
          ],
          Z: [
            { row: 0, col: 0 },
            { row: 0, col: 1 },
            { row: 1, col: 1 },
            { row: 1, col: 2 },
          ],
          J: [
            { row: 0, col: 0 },
            { row: 0, col: 1 },
            { row: 0, col: 2 },
            { row: 1, col: 2 },
          ],
        }[blueprint]
      : [
          { row: 0, col: 0 },
          { row: 0, col: 1 },
          { row: 0, col: 2 },
          { row: 0, col: 3 },
          { row: 1, col: 0 },
          { row: 1, col: 1 },
          { row: 1, col: 2 },
          { row: 1, col: 3 },
          { row: 2, col: 0 },
          { row: 2, col: 1 },
          { row: 2, col: 2 },
          { row: 2, col: 3 },
        ];

  const blueprintSizes: (blueprint?: Blueprint) => Coordinates = (
    blueprint
  ) => {
    const pips = blueprintPips(blueprint);
    const pipRows = pips.map(({ row }) => row);
    const pipCols = pips.map(({ col }) => col);
    const rowMin = Math.min(...pipRows);
    const rowMax = Math.max(...pipRows);
    const colMin = Math.min(...pipCols);
    const colMax = Math.max(...pipCols);
    const row = rowMax - rowMin + 1;
    const col = colMax - colMin + 1;
    return { row, col };
  };

  const numToBlueprint: (num: number) => Blueprint = (num) =>
    (["O", "I", "T", "S", "L", "J", "Z", "T", "I", "O"] as Blueprint[])[num];

  const blueprintPipsCoords: (
    blueprint?: Blueprint
  ) => { pip: Coordinates; coord: Coordinates }[] = (blueprint) =>
    blueprintPips(blueprint).map((pip) => ({
      pip,
      coord: scaleCoordinates(
        subtractCoordinates(
          scaleCoordinates(blueprintSizes(blueprint), 0.5),
          pip
        ),
        PIP_SIZE
      ),
    }));

  const TokensPrintSheet = () => (
    <svg
      height="200vmin"
      width="125vmin"
      preserveAspectRatio="xMinYMin slice"
      viewBox="0 0 100 200"
      style={{ overflow: "visible" }}
    >
      <line
        key={`clmiddle`}
        x1={(TOKEN_SIZE + PADDING) * Blueprints.length - PADDING / 2}
        y1={0}
        x2={(TOKEN_SIZE + PADDING) * Blueprints.length - PADDING / 2}
        y2="200"
        stroke="black"
        strokeWidth={CONTROL_LINE}
      />
      {numberlist(NUM_PLAYERS * Resources.length).map((i) =>
        numberlist(Blueprints.length * 2)
          .map(numToBlueprint)
          .map((blueprint, j) => (
            <BlueprintToken
              key={j}
              coord={{
                row: (TOKEN_SIZE + PADDING) * i,
                col: (TOKEN_SIZE + PADDING) * j,
              }}
              blueprint={blueprint}
            />
          ))
      )}
    </svg>
  );

  return (
    <>
      <Map map={props.G.map} actionSelection={displayPlayer.actionSelection} />
      <Market market={props.G.market} />
      <Player player={displayPlayer} />
      {props.playerID ? null : (
        <>
          <TokensPrintSheet />
          {props.G.players.map((player) => (
            <>
              <Player player={player} />
              <br />
              <br />
              <br />
            </>
          ))}
        </>
      )}
    </>
  );
}
