const miniMap = miniMaps[0];

export const startCorners: (distanceFromCorner?: number) => Coordinates[] = (
  distanceFromCorner = 1
) => [
  { row: 0, col: 0 },
  { row: GAME_SIZE - distanceFromCorner, col: GAME_SIZE - distanceFromCorner },
  { row: 0, col: GAME_SIZE - distanceFromCorner },
  { row: GAME_SIZE - distanceFromCorner, col: 0 },
];

const scaleMatrix: <T>(matrix: T[][], factor: number) => T[][] = (
  matrix,
  factor
) =>
  numberlist(factor * matrix.length).map((_, i) =>
    numberlist(factor * matrix.length).map(
      (_, j) => matrix[Math.trunc(i / factor)][Math.trunc(j / factor)]
    )
  );

const getTerrain = (map: TerrainType[][], coord: Coordinates) =>
  map[coord.row][coord.col];

const smoothMap: (map: TerrainType[][], pass?: number) => TerrainType[][] = (
  map
) => {
  const newMap = map.slice();
  let changed = false;
  map.forEach((row, i) =>
    row.forEach((col, j) => {
      const currentTerrain = col;
      const referenceTerrains = [
        currentTerrain,
        ...getCoordinateNeighbours({ row: i, col: j }).map((at) =>
          getTerrain(map, at)
        ),
      ];
      const avg = referenceTerrains.reduce(
        (acc, t) => acc + terrainToIndex(t) / referenceTerrains.length,
        0
      );
      newMap[i][j] = TerrainSmoothing[Math.round(avg)];
      if (newMap[i][j] !== currentTerrain) {
        changed = true;
      }
    })
  );
  if (changed) return smoothMap(newMap);
  return newMap;
};

export const terrainStats = (map: TerrainType[][]) =>
  map
    .flat()
    .reduce(
      (acc: Map<TerrainType, number>, terrain: TerrainType) =>
        acc.set(terrain, zeroKey(acc.get(terrain)) + 1),
      new Map()
    );

const applyTerrainFeature: (
  map: TerrainType[][],
  feature: boolean[][],
  maskCoord: Coordinates,
  type: TerrainType
) => TerrainType[][] = (map, feature, maskCoord, type) => {
  const featureTerrains: Coordinates[] = featureMaskToTerrains(
    feature,
    maskCoord
  );
  const featureMap = map.slice();
  let changed = false;
  featureTerrains.forEach((coord) => {
    if (featureMap[coord.row][coord.col] !== type) {
      changed = true;
    }
    featureMap[coord.row][coord.col] = type;
  });
  if (changed) return smoothMap(featureMap);
  return featureMap;
};

const randomCoords: (random: Random, mask: boolean[][]) => Coordinates = (
  random,
  mask
) => ({
  row: Math.trunc(random() * (GAME_SIZE - mask.length)),
  col: Math.trunc(random() * (GAME_SIZE - mask[0].length)),
});

const differenceFromSimilarTerrain = (
  map: TerrainType[][],
  frequencies: [TerrainType, number][],
  coord: Coordinates,
  mask: boolean[][]
) =>
  getSelectionNeighbours(featureMaskToTerrains(mask, coord)).reduce(
    (acc, c) =>
      acc +
      (frequencies.find((f) => getTerrain(map, c) === f[0]) ||
        frequencies[0])[1],
    0
  );

const getSomeTerrainCoord: (
  map: TerrainType[][],
  frequencies: [TerrainType, number][],
  mask: boolean[][],
  random: Random,
  pass: number
) => Coordinates = (map, frequencies, mask, random, pass) =>
  numberlist(pass).reduce(
    ({ score, coord }) => {
      const newCoord = randomCoords(random, mask);
      const newScore = differenceFromSimilarTerrain(
        map,
        frequencies,
        newCoord,
        mask
      );
      return score > newScore
        ? { score, coord }
        : { coord: newCoord, score: newScore };
    },
    {
      score: 0,
      coord: { row: 0, col: 0 },
    }
  ).coord;

const smoothTerrainFeature = (
  map: TerrainType[][],
  mask: boolean[][],
  coord: Coordinates,
  terrain: TerrainType
): TerrainType[][] =>
  numberlist(mask.length).reduce(
    (accFeature, _) => applyTerrainFeature(accFeature, mask, coord, terrain),
    map
  );

const featureMask = (size: number) =>
  numberlist(size).map((_) => numberlist(size).map((_) => true));

const balanceMap: (map: TerrainType[][]) => TerrainType[][] = (map) => {
  const mask = featureMask(FEATURE_SIZE);
  return startCorners(FEATURE_SIZE).reduce(
    (accMap, corner, i) =>
      smoothTerrainFeature(accMap, mask, corner, Terrains[i]),
    map
  );
};

export const raiseMap: (
  map: TerrainType[][],
  random: Random,
  pass?: number
) => TerrainType[][] = (map, random, pass = GAME_SIZE ** 2) => {
  const mask = featureMask(FEATURE_SIZE);
  return numberlist(pass).reduce((accPass, i) => {
    const stats = terrainStats(accPass);
    const featureTerrain = leastFrequentTerrain(stats);
    const destinationTerrain = mostFrequentTerrain(stats);
    const frequencies = [...stats.entries()];
    const TOLERANCE = 0.1;
    if (
      Math.abs(
        zeroKey(stats.get(destinationTerrain)) /
          zeroKey(stats.get(featureTerrain))
      ) <=
      1 + TOLERANCE
    ) {
      return accPass;
    }
    const featureCoord = getSomeTerrainCoord(
      map,
      frequencies,
      mask,
      random,
      pass - i
    );
    return balanceMap(
      smoothTerrainFeature(accPass, mask, featureCoord, featureTerrain)
    );
  }, balanceMap(map));
};

export const newMap: (random: Random) => TerrainMap = (random) => {
  const scaleMap = scaleMatrix(miniMap, GAME_SIZE / miniMap.length);
  const terrainMap = raiseMap(scaleMap, random);
  const stats = terrainStats(terrainMap);
  console.log(
    stats,
    zeroKey(stats.get(mostFrequentTerrain(stats))) /
      zeroKey(stats.get(leastFrequentTerrain(stats)))
  );
  return terrainMap.map((terrainRow, row) =>
    terrainRow.map((terrain, col) => ({
      type: terrain,
      state: "free",
      coord: { row, col },
    }))
  );
};

export const terrainCost = (
  coords: Coordinates[],
  map: TerrainMap,
  bag: ResourceBag = emptyBag
) =>
  coords
    .map((c) => map[c.row][c.col].type)
    .filter(unique)
    .reduce(
      (bag, res) =>
        addBags(bag, {
          ...emptyBag,
          [res]: 1,
        }),
      bag
    );

export const addCoordinates: (
  c1: Coordinates,
  c2: Coordinates
) => Coordinates = (c1, c2) => ({
  row: c2.row + c1.row,
  col: c2.col + c1.col,
});

export const scaleCoordinates: (
  c: Coordinates,
  factor: number
) => Coordinates = (c, factor) => ({
  row: c.row * factor,
  col: c.col * factor,
});

export const subtractCoordinates: (
  c1: Coordinates,
  c2: Coordinates
) => Coordinates = (c1, c2) => ({
  row: c2.row - c1.row,
  col: c2.col - c1.col,
});

export const unitCoordinates: (c1: Coordinates) => Coordinates = (c) => ({
  row: c.row === 0 ? 0 : c.row / Math.abs(c.row),
  col: c.col === 0 ? 0 : c.col / Math.abs(c.col),
});

export const VECTOR_ORIENTATIONS: Coordinates[] = [
  { row: -1, col: 0 },
  { row: 1, col: 0 },
  { row: 0, col: -1 },
  { row: 0, col: 1 },
];

const mostFrequentTerrain = (stats: Map<TerrainType, number>) =>
  Terrains.reduce(
    (acc, t) => (zeroKey(stats.get(t)) > zeroKey(stats.get(acc)) ? t : acc),
    Terrains[0]
  );

const leastFrequentTerrain = (stats: Map<TerrainType, number>) =>
  Terrains.reduce(
    (acc, t) => (zeroKey(stats.get(t)) < zeroKey(stats.get(acc)) ? t : acc),
    Terrains[Terrains.length - 1]
  );

const featureMaskToTerrains = (feature: boolean[][], maskCoord: Coordinates) =>
  feature
    .flatMap((row, i) =>
      row.map((_, j) => ({
        row: i + maskCoord.row,
        col: j + maskCoord.col,
      }))
    )
    .filter(isWithinMap);
