import { ActionTypes, type ActionType } from "./ActionType";
import type { Blueprint } from "./Blueprint";
import {
  actionCost,
  blueprintMastery,
  isUniqueBlueprint,
  isValidSpaceSelection,
  upgradeGrid,
} from "./Grid";
import type { Coordinates } from "./Map";
import {
  getAdjacentToSelection,
  sameCoordinates,
  subtractCoordinates,
  applyMapEffect,
  terrainCost,
} from "./Map";
import { addTokenToBag, discardMarketToken } from "./Market";
import type { Player } from "./Player";
import type { SwarmsState } from "./Swarms";
import { getBoundingBox, terrainFromMap } from "./Terrain";
import { deepAssign } from "./utils";

export type Action<T extends ActionType> = {
  actionType: T;
  row: number;
  blueprint?: Blueprint;
  value: number;
};

type ActionRunner = (G: SwarmsState, id: number) => SwarmsState;

const selectAction = (
  G: SwarmsState,
  id: number,
  row: number,
  actionType: ActionType
) =>
  deepAssign(
    G,
    G.players[id].grid[row][actionType],
    "players",
    (p) => p.id === id,
    "actionSelection",
    "action"
  );

export const upgrade: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const token = G.market.stalls[0];
  if (token === undefined) {
    return G;
  }
  const action = currentPlayer.actionSelection.action;
  if (action === undefined || action.blueprint !== undefined) {
    return G;
  }
  if (
    !isValidSpaceSelection(
      currentPlayer.grid,
      action.row,
      action.actionType,
      token
    )
  ) {
    return G;
  }

  const grid = upgradeGrid(currentPlayer.grid, action, token);
  const market = addTokenToBag(G.market, token);

  const upgradedGridGame = deepAssign(
    { ...G, market },
    grid,
    "players",
    (p) => p.id === id,
    "grid"
  );

  return selectAction(upgradedGridGame, id, action.row, action.actionType);
};

const expand: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  if (currentPlayer.actionSelection.action === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const location = currentPlayer.actionSelection.location;
  const cost = terrainCost(
    location,
    G.map,
    actionCost(currentPlayer.actionSelection.action)
  );
  const map = applyMapEffect(G.map, location, {
    state: "occupied",
    owner: id,
  });
  const coins =
    currentPlayer.coins + blueprintMastery(currentPlayer.grid, token);
  const swarm = [...currentPlayer.swarm, ...location];
  const player = deepAssign(
    { ...currentPlayer, swarm, coins },
    cost,
    "actionSelection",
    "cost",
    "resources"
  );
  const market = discardMarketToken(G.market);
  return deepAssign(
    { ...G, map, market },
    player,
    "players",
    (p) => p.id === id
  );
};

const build: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  if (currentPlayer.actionSelection.action?.blueprint === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const destination = currentPlayer.actionSelection.location;
  const cost = actionCost(currentPlayer.actionSelection.action);
  const map = applyMapEffect(G.map, destination, {
    state: "built",
    blueprint: token,
  });
  const coins =
    currentPlayer.coins + blueprintMastery(currentPlayer.grid, token);
  const city = [...currentPlayer.city, ...destination];
  const swarm = currentPlayer.swarm.filter(
    (c1) => !destination.some((c2) => sameCoordinates(c1, c2))
  );
  const player = deepAssign(
    { ...currentPlayer, city, swarm, coins },
    cost,
    "actionSelection",
    "cost",
    "resources"
  );
  const market = discardMarketToken(G.market);
  return deepAssign(
    { ...G, map, market },
    player,
    "players",
    (p) => p.id === id
  );
};

const trade: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  if (currentPlayer.actionSelection.action === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const deliveryDistance = (
    location: Coordinates[],
    destination: Coordinates[]
  ) =>
    Math.max(
      ...Object.values(
        subtractCoordinates(
          getBoundingBox(location).topLeft,
          getBoundingBox(destination).topLeft
        )
      ).map(Math.abs)
    );
  const distance = deliveryDistance(
    currentPlayer.actionSelection.location,
    currentPlayer.actionSelection.destination
  );
  const targetOpponents = getAdjacentToSelection(
    currentPlayer.actionSelection.destination
  )
    .map((c) => terrainFromMap(G.map, c))
    .map((t) => (t.state === "occupied" ? t.owner : id))
    .filter((playerId) => playerId !== id)
    .reduce(
      (acc: number[], p: number) => (acc.includes(p) ? acc : [...acc, p]),
      []
    );
  const targetDeliveries = targetOpponents
    .map((targetId) => G.players[targetId])
    .map((player) => ({
      id: player.id,
      tradeAction: player.grid
        .map((row) => row.trade)
        .find(
          (action) =>
            action.blueprint === undefined &&
            isUniqueBlueprint(player.grid, action.row, action.actionType, token)
        ),
    }))
    .filter(({ tradeAction }) => tradeAction !== undefined) as {
    id: number;
    tradeAction: Action<ActionType>;
  }[];
  const cost = terrainCost(
    currentPlayer.actionSelection.destination,
    G.map,
    actionCost(currentPlayer.actionSelection.action, distance)
  );
  const map = applyMapEffect(G.map, currentPlayer.actionSelection.destination, {
    state: "occupied",
    owner: id,
  });
  const price =
    targetDeliveries.reduce((acc, p) => acc + p.tradeAction.value, 0) +
    blueprintMastery(currentPlayer.grid, token);
  const amount = [...G.market.stalls, ...G.market.discard].filter(
    (t) => t === token
  ).length;
  const coins = price * amount + currentPlayer.coins;
  const swarm = [
    ...currentPlayer.swarm,
    ...currentPlayer.actionSelection.destination,
  ];
  const travel = currentPlayer.travel + distance;
  const player = deepAssign(
    { ...currentPlayer, swarm, travel, coins },
    cost,
    "actionSelection",
    "cost",
    "resources"
  );
  const players = G.players.map((p) => {
    if (p.id === id) {
      return player;
    }
    const target = targetDeliveries.find((pd) => pd.id === p.id);
    if (target === undefined) {
      return p;
    }
    const targetPrice = target.tradeAction.value;
    const targetAmount =
      p.city
        .map((b) => terrainFromMap(G.map, b))
        .filter((t) => t.state === "built" && t.blueprint === token).length / 4;
    const coins = p.coins + targetPrice * targetAmount;
    const grid = upgradeGrid(p.grid, target.tradeAction, token);
    return {
      ...p,
      grid,
      coins,
    };
  });
  const market = discardMarketToken(G.market);
  return { ...G, map, market, players };
};

export const scienceScore = (player: Player) => [
  player.swarm.length / 4,
  player.city.length,
  player.travel,
  player.grid
    .flatMap((row) => ActionTypes.map((a) => row[a].blueprint))
    .filter((bp) => bp !== undefined).length,
];

const research: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const action = currentPlayer.actionSelection.action;
  if (action === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const cost = actionCost(action);
  const coins =
    currentPlayer.coins +
    scienceScore(currentPlayer)[action.row] *
      blueprintMastery(currentPlayer.grid, token);
  const player = deepAssign(
    { ...currentPlayer, coins },
    cost,
    "actionSelection",
    "cost",
    "resources"
  );
  const players = G.players.map((p) => {
    if (p.id === id) {
      return player;
    }
    const grid = upgradeGrid(p.grid, p.grid[action.row].science, token);
    return { ...p, grid };
  });
  const market = discardMarketToken(G.market);
  return { ...G, market, players };
};

export const getActionFromType: (actionType: ActionType) => ActionRunner = (
  actionType
) => {
  switch (actionType) {
    case "swarm":
      return expand;
    case "city":
      return build;
    case "trade":
      return trade;
    case "science":
      return research;
  }
};
